'use server';

import mysql from 'mysql2/promise';
import type { Block } from '@/lib/template-utils';

async function getDbConnection() {
    const { MYSQL_HOST, MYSQL_USER, MYSQL_PASSWORD, MYSQL_DATABASE, MYSQL_PORT } = process.env;
    if (!MYSQL_HOST || !MYSQL_USER || !MYSQL_DATABASE) {
        throw new Error('Faltan las variables de entorno de la base de datos. Por favor, configúralas.');
    }
    return mysql.createConnection({
        host: MYSQL_HOST,
        port: MYSQL_PORT ? parseInt(MYSQL_PORT, 10) : 3306,
        user: MYSQL_USER,
        password: MYSQL_PASSWORD,
        database: MYSQL_DATABASE,
    });
}

export interface Template {
    id_plantilla: number;
    nombre: string;
    asunto_predeterminado: string;
    contenido: Block[];
    fecha_creacion: string;
    categoria: string;
}

export async function getTemplatesAction(): Promise<Template[]> {
    let connection;
    try {
        connection = await getDbConnection();
        const [rows] = await connection.execute('SELECT id_plantilla, nombre, asunto_predeterminado, contenido, fecha_creacion, categoria FROM plantillas ORDER BY fecha_creacion DESC');
        
        const templates = (rows as any[]).map(row => {
            let parsedContenido;
            if (typeof row.contenido === 'string') {
                try {
                    parsedContenido = row.contenido ? JSON.parse(row.contenido) : [];
                } catch (error) {
                    console.error(`Error al parsear el JSON del contenido de la plantilla ID ${row.id_plantilla}:`, row.contenido);
                    parsedContenido = [];
                }
            } else {
                parsedContenido = row.contenido || [];
            }
            return {
                ...row,
                contenido: parsedContenido,
            };
        });
        
        return templates as Template[];
    } catch (error) {
        console.error('Error al obtener las plantillas:', error);
        return [];
    } finally {
        if (connection) await connection.end();
    }
}

export async function getTemplateAction(id: number): Promise<Template | null> {
    let connection;
    try {
        connection = await getDbConnection();
        const [rows] = await connection.execute('SELECT id_plantilla, nombre, asunto_predeterminado, contenido, fecha_creacion, categoria FROM plantillas WHERE id_plantilla = ?', [id]);
        
        if ((rows as any[]).length === 0) return null;

        const row = (rows as any[])[0];
        let parsedContenido;
        if (typeof row.contenido === 'string') {
            try {
                parsedContenido = row.contenido ? JSON.parse(row.contenido) : [];
            } catch (error) {
                console.error(`Error al parsear el JSON del contenido de la plantilla ID ${row.id_plantilla}:`, row.contenido);
                parsedContenido = [];
            }
        } else {
            parsedContenido = row.contenido || [];
        }
        return {
            ...row,
            contenido: parsedContenido,
        } as Template;

    } catch (error) {
        console.error(`Error al obtener la plantilla ${id}:`, error);
        return null;
    } finally {
        if (connection) await connection.end();
    }
}

export async function saveTemplateAction(templateData: {
    id?: number,
    nombre: string,
    asunto_predeterminado: string,
    contenido: Block[],
    categoria: string
}): Promise<{ success: boolean; message: string, id?: number }> {
    const { id, nombre, asunto_predeterminado, contenido, categoria } = templateData;
    const contenidoJson = JSON.stringify(contenido);
    let connection;

    const logData = { id, nombre };

    try {
        connection = await getDbConnection();
        if (id) {
            await connection.execute(
                'UPDATE plantillas SET nombre = ?, asunto_predeterminado = ?, contenido = ?, categoria = ? WHERE id_plantilla = ?',
                [nombre, asunto_predeterminado, contenidoJson, categoria, id]
            );
            console.log('Plantilla actualizada con éxito:', logData);
            return { success: true, message: 'Plantilla actualizada con éxito.', id };
        } else {
            const [result] = await connection.execute(
                'INSERT INTO plantillas (nombre, asunto_predeterminado, contenido, categoria) VALUES (?, ?, ?, ?)',
                [nombre, asunto_predeterminado, contenidoJson, categoria]
            );
            const insertId = (result as any).insertId;
            console.log('Plantilla creada con éxito:', { ...logData, id: insertId });
            return { success: true, message: 'Plantilla creada con éxito.', id: insertId };
        }
    } catch (error) {
        console.error('Error al guardar la plantilla:', { ...logData, error: (error as Error).message });
        return { success: false, message: `Error al guardar la plantilla: ${(error as Error).message}` };
    } finally {
        if (connection) await connection.end();
    }
}

export async function deleteTemplateAction(id: number): Promise<{ success: boolean; message: string }> {
    let connection;
    try {
        connection = await getDbConnection();
        await connection.execute('DELETE FROM plantillas WHERE id_plantilla = ?', [id]);
        return { success: true, message: 'Plantilla eliminada con éxito.' };
    } catch (error) {
        console.error(`Error al eliminar la plantilla ${id}:`, error);
        return { success: false, message: `Error al eliminar la plantilla: ${(error as Error).message}` };
    } finally {
        if (connection) await connection.end();
    }
}
