'use client';

import { useState, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Save } from 'lucide-react';
import { useToast } from "@/hooks/use-toast";
import { getTemplatesAction, Template } from '@/actions/template-actions';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { generateHtmlFromBlocks } from '@/lib/template-utils';

/**
 * Componente de cliente para el editor de eventos y certificados.
 * Permite definir los detalles del evento y personalizar la plantilla del certificado.
 */
export function EventEditor() {
  const { toast } = useToast();
  const [eventName, setEventName] = useState('');
  const [eventDescription, setEventDescription] = useState('');
  const [emailSubject, setEmailSubject] = useState('');
  const [emailBody, setEmailBody] = useState('');
  const [eventTemplates, setEventTemplates] = useState<Template[]>([]);

  useEffect(() => {
    async function fetchEventTemplates() {
      const allTemplates = await getTemplatesAction();
      const filteredTemplates = allTemplates.filter(t => t.categoria === 'Evento');
      setEventTemplates(filteredTemplates);
    }
    fetchEventTemplates();
  }, []);

  const handleTemplateChange = (templateId: string) => {
    const selectedTemplate = eventTemplates.find(t => t.id_plantilla.toString() === templateId);
    if (selectedTemplate) {
      setEmailSubject(selectedTemplate.asunto_predeterminado);
      // Asumimos que el cuerpo del correo será HTML generado a partir de los bloques
      const htmlBody = generateHtmlFromBlocks(selectedTemplate.contenido);
      setEmailBody(htmlBody);
    }
  };

  const handleSaveEvent = () => {
    // Aquí reunirías todos los datos y los enviarías a una acción de servidor.
    console.log({ eventName, eventDescription, emailSubject, emailBody });
    toast({
      title: "Evento Guardado",
      description: "La configuración del evento y el certificado se ha guardado.",
    });
  };

  return (
    <div className="space-y-8">
       <div>
        <div className="flex items-center justify-between mb-2">
            <h1 className="text-3xl font-headline font-bold">Editor de Eventos</h1>
            <Button onClick={handleSaveEvent}>
                <Save className="mr-2 h-4 w-4" />
                Guardar Evento
            </Button>
        </div>
        <p className="text-muted-foreground">
          Crea un nuevo evento y personaliza el certificado de asistencia.
        </p>
      </div>

      <Card>
        <CardHeader>
          <CardTitle>1. Detalles del Evento</CardTitle>
          <CardDescription>
            Configura la información básica del evento.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="event-name">Nombre del Evento</Label>
            <Input id="event-name" placeholder="Ej: Conferencia Anual de Tecnología" value={eventName} onChange={(e) => setEventName(e.target.value)} />
          </div>
          <div className="space-y-2">
            <Label htmlFor="event-description">Descripción del Evento</Label>
            <Textarea id="event-description" placeholder="Describe brevemente de qué trata el evento..." value={eventDescription} onChange={(e) => setEventDescription(e.target.value)} />
          </div>
        </CardContent>
      </Card>

      <Card>
        <CardHeader>
          <CardTitle>2. Detalles del Correo de Invitación</CardTitle>
          <CardDescription>
            Selecciona una plantilla para la invitación o redacta el correo.
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div className="space-y-2">
            <Label>Plantilla de Correo</Label>
            <Select onValueChange={handleTemplateChange}>
              <SelectTrigger>
                <SelectValue placeholder="Seleccionar una plantilla de evento" />
              </SelectTrigger>
              <SelectContent>
                {eventTemplates.map(template => (
                  <SelectItem key={template.id_plantilla} value={template.id_plantilla.toString()}>
                    {template.nombre}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
          <div className="space-y-2">
            <Label htmlFor="email-subject">Asunto del Correo</Label>
            <Input id="email-subject" placeholder="Asunto de la invitación" value={emailSubject} onChange={(e) => setEmailSubject(e.target.value)} />
          </div>
          <div className="space-y-2">
            <Label htmlFor="email-body">Cuerpo del Correo (HTML)</Label>
            <Textarea id="email-body" placeholder="El contenido de tu correo..." rows={10} value={emailBody} onChange={(e) => setEmailBody(e.target.value)} />
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
